
.wrapper{
    width: 100%;
    height: 600px;
    background-color: #dbe8f3;
}



.user{
    display: grid;
    width: 98%;
    height: 100px;
    border: 1px solid #1c1a1a;
    background-color: aliceblue;
    color: #1c1a1a;
    grid-template-columns: 1fr 5fr ;
    grid-template-rows: 1fr ;

}

.user img{
    width: 50px;
    border-radius: 50%;
    border: 1px solid #24781f;
}
.imgButton button{
    background-color: #3c1053;
    color: #fff;
    text-shadow: #1c1a1a;
    text-align: center;
    font-size: 30px;
    width: 170px;
}

.imgButton{
    display: flex;

    flex-direction: column;

}


